.daily {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.daily .description {
  width: 75vw;
  margin: auto;
  text-align: center;
}

.daily .items {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  color: chocolate;
}

.daily .items .ddcontainer {
  opacity: .7;
}

.daily .items .ddcontainer:hover {
  transition: background-color 0.5s ease;
  opacity: 1;
  cursor: pointer;
}

.daily .items:focus {
  outline: none;
}

.daily .box {
border: 2px solid black;
border-radius: 4px;
min-width: 300px;
min-height: 300px;
background-image: linear-gradient(to bottom,rgba(255, 255, 255, .8), rgba(255, 255, 255, 0)), url(../assets/map.webp);
}

.daily .no-webp .box  {
  background-image: linear-gradient(to bottom,rgba(255, 255, 255, .8), rgba(255, 255, 255, 0)), url(../assets/fallback/map.png);
}